<template>
  <div class="Login">
    <h3>用户登录</h3>
    <div class="content">
      <div>
        <label for="username">用户名</label>
        <input type="text" class="user" id="username" ref="inputUsername"/>
      </div>
      <div class="chooseAvatar">
        <label for="avatar">选择头像</label>
        <ul class="avatarWrap">
          <li
            v-for="(item,index) in imgUrl"
            :key="item"
          >
            <img :src="require('../assets/avatar/' + item)" alt @click="clickImg(index,item)"
            :class="{active:currentIndex===index?true:false}"/>
          </li>
        </ul>
      </div>
      <button class="button" @click="loginRoom">登录</button>
    </div>
  </div>
</template>

<script>
import io from 'socket.io-client' //引入socket.io-client
export default {
  name: 'Login',
  data() {
    return {
      imgUrl: [
        'one.jpg',
        'two.jpg',
        'four.jpg',
        'three.jpeg',
        'eight.jpg',
        'seven.jpg',
        'six.jpg',
        'five.jpg',
      ],
      currentIndex: 0,
      currentImg:"one.jpg"
    }
  },
  methods: {
    clickImg(index,item) {
      this.currentIndex = index
      this.currentImg = item
    },
    loginRoom(){
      // 1.获取用户名
      const username = this.$refs.inputUsername.value
      if(!username.trim()){
        alert("请输入用户名")
        return
      }
      // 2.获取到选择的头像
      console.log(this.currentImg)
      // 3.需要告诉socket io服务，进行登录
      io.emit('login',{
        username,
        avatar:this.currentImg
      })
    }
  },
  mounted() {
    /**
     * 聊天室的主要功能
     */
    // 1.连接服务器
    io('http://localhost:3000')
    // 2.点击按钮登录

  },
}
</script>

